export default {
	data() {
		return {
			pid: "",
			labelList: [{
					name: '全部',
					number: 25,
					type: 'all'
				},
				{
					name: '好评',
					number: 23,
					type: 'good'
				},
				{
					name: '中评',
					number: 1,
					type: 'secondary'
				},
				{
					name: '差评',
					number: 1,
					type: 'poor'
				},
				{
					name: '有图',
					number: 12,
					type: 'img'
				},
				{
					name: '视频',
					number: 2,
					type: 'video'
				},
				{
					name: '追加',
					number: 2,
					type: 'append'
				}
			],
			labelIndex: 0,
			ratingsList: [
				{
					id: 3,
					username: "小黑狗",
					face: "/static/img/face.jpg",
					date: '2019-04-21',
					spec: "规格: XL",
					grade: "poor",
					first: {
						content: "好评，看图",
						img: ["https://ae01.alicdn.com/kf/HTB111soTbvpK1RjSZPiq6zmwXXaB.jpg",
							"https://ae01.alicdn.com/kf/HTB1O2TRTmzqK1RjSZPcq6zTepXa4.jpg"
						],
						video: []
					}
				},
				{
					id: 3,
					username: "小黑狗",
					face: "/static/img/face.jpg",
					date: '2019-04-21',
					spec: "规格: XL",
					grade: "secondary",
					first: {
						content: "系统默认好评",
						img: [],
						video: []
					}
				}
			],
			videoDirection: 0,
			showFullscreenBtn: true,
			showPlayBtn: true,
			isPlayVideo: true,
			videoSrc: ''

		};
	},
	onLoad(options) {
		this.pid = options.pid;
		this.getmsg();
	},
	onReady: function(res) {
		this.videoContext = uni.createVideoContext('myVideo')
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		uni.showToast({
			title: '触发上拉加载'
		});
	},
	methods: {
		getmsg() {
			uni.request({
				//仅为示例，并非真实接口地址。
				url: "http://120.79.19.253:10086/Leftmessage",
				data: {
					type: "get",
					pid: this.pid
				},
				success: (res) => {
					console.log(res.data.status, "fghjklkhgfhjhgfhjkhgfjkhgggggggggggggggggggggg");
					if (res.data.status == 0) {
						this.ratingsList.push({
							id: 0,
							username: "系统",
							face: "/static/img/face.jpg",
							date: '00-00-00',
							spec: "规格: 常规",
							grade: "secondary",
							first: {
								content: "系统默认好评",
								img: ["https://ae01.alicdn.com/kf/HTB111soTbvpK1RjSZPiq6zmwXXaB.jpg",
									"https://ae01.alicdn.com/kf/HTB1O2TRTmzqK1RjSZPcq6zTepXa4.jpg"
								],
								video: []
							}
						})
					} else {
						var mydatamag = res.data.data;
						for (var item of mydatamag) {
							this.ratingsList.push({
								id: item.uid,
								username: item.nickname,
								face: item.image,
								date: item.time,
								spec: "规格: 常规",
								grade: "secondary",
								first: {
									content: item.message,
									img: [],
									video: []
								}
							})
						}
					}
				}
			})
		},
		loadRatings(index) {
			this.labelIndex = index;
			uni.showToast({
				title: "切换评论列表"
			})
			//实际应用中，请求对应类型的评论列表，覆盖this.ratingsList
			/*
			let type = this.labelList[index].type; // 评论类型
			......
			*/
		},
		playVideo(path) {
			this.videoSrc = path;
			// this.isPlayVideo = true;
			this.$nextTick(function() {
				this.videoContext.requestFullScreen({
					direction: 0
				});
			});

		},
		stopPlayVideo() {
			this.videoContext.pause();
		},
		videoPause() {
			// this.isPlayVideo = false;
			this.videoSrc = '';
		},
		viderFullscreen(e) {
			if (e.detail.fullScreen) {
				this.videoContext.play();
			} else {
				this.stopPlayVideo();
			}
		},
		showBigImg(src, srclist) {
			uni.previewImage({
				current: src,
				urls: srclist
			});
		}
	},
}
